/** SEARCH THE FACTFINDER **/
.searchandfilter {
  padding-bottom: 40px;
  border-bottom: #045593 1px solid;
  display: block;
  font-family: "realistnarrow";
}
.searchandfilter h4 {
  text-transform: uppercase;
  border-bottom: #045593 2px solid;
}
.searchandfilter h4 span:last-child {
  font-weight: 700;
}
.searchandfilter label {
  display: block;
}
.searchandfilter ul {
  padding-left: 0;
}
.searchandfilter ul::after {
  content: "";
  display: block;
  clear: left;
}
.searchandfilter ul li {
  float: left;
  padding-top: 0;
}
.searchandfilter li:nth-child(odd) {
  width: 27%;
}
.searchandfilter li:nth-child(even) {
  width: 46%;
  padding: 0 40px;
  border-left: #045593 1px solid;
  border-right: #045593 1px solid;
}
.searchandfilter li:nth-child(1) {
  padding-right: 40px;
}
.searchandfilter li:nth-child(3) {
  padding-left: 40px;
}
.searchandfilter .sf-field-category li {
  width: auto;
  float: none;
  border: none;
  padding-left: 0;
  padding-right: 0;
  display: grid;
  grid-template-columns: 1em auto;
}
.searchandfilter select {
  width: 100%;
}
.searchandfilter input[type=checkbox] {
  cursor: pointer;
  appearance: none;
  margin: 0;
  font: inherit;
  width: 23px;
  height: 23px;
  display: grid;
  place-content: center;
}
.searchandfilter input[type=checkbox]::before {
  content: "";
  box-shadow: inset 11px 11px #045593;
  width: 11px;
  height: 11px;
  transform: scale(0);
}
.searchandfilter input[type=checkbox]:checked::before {
  transform: scale(1);
}
.searchandfilter input[type=checkbox]:focus,
.searchandfilter input[type=checkbox]:active,
.searchandfilter input[type=checkbox],
.searchandfilter input[type=text], .searchandfilter select {
  border-color: #FF952A !important;
}
.searchandfilter input[type=checkbox],
.searchandfilter input[type=text], .searchandfilter select {
  border-width: 2px;
  border-style: solid;
}
.searchandfilter input[type=text], .searchandfilter select {
  border-radius: revert;
  height: revert;
  padding: 7px;
  color: #045593;
  font-size: 1.3rem;
}

